@-webkit-keyframes JoinLetter{
  0%{
    opacity:0;
    letter-spacing:1.6rem
  }

  100%{
    opacity:1;
    letter-spacing:0.2rem
  }
}

@keyframes JoinLetter{
  0%{
    opacity:0;
    letter-spacing:1.6rem
  }

  100%{
    opacity:1;
    letter-spacing:0.2rem
  }
}

.get-button-layout{
  min-height:100vh;
  width:100%;
  padding-top:6rem;
  padding-bottom:6rem;
  display:flex;
  align-items:center;
  justify-content:center
}

.get-button-layout .get-button-container{
  z-index:10;
  margin-left:auto;
  margin-right:auto;
  display:flex;
  width:91.666667%;
  flex-direction:column;
  border-radius:0.5rem;
  --tw-bg-opacity:1;
  background-color:rgba(255, 255, 255, var(--tw-bg-opacity));
  --tw-bg-opacity:0.5;
  padding-left:1.5rem;
  padding-right:1.5rem;
  padding-top:2rem;
  padding-bottom:2rem
}

@media (min-width: 481px){
  .get-button-layout .get-button-container{
    width:80%;
    padding:2rem;
    padding-bottom:3.5rem
  }
}

@media (min-width: 992px){
  .get-button-layout .get-button-container{
    width:66.666667%
  }
}

@media (min-width: 1200px){
  .get-button-layout .get-button-container{
    width:50%
  }
}

.get-button-layout .get-button-container{
  box-shadow:0 1px 12px 1px rgba(69,65,78,0.1)
}

.get-button-layout .get-button-container h1{
  text-align:center;
  font-size:1.125rem;
  line-height:1.75rem;
  font-weight:500;
  letter-spacing:0.05em
}

.get-button-layout .get-button-container .input-container{
  margin-top:2rem;
  display:flex;
  width:100%;
  flex-direction:column;
  align-items:center
}

.get-button-layout .get-button-container .input-container .input-label{
  margin-bottom:0.5rem;
  font-weight:500;
  --tw-text-opacity:1;
  color:rgba(41, 7, 89, var(--tw-text-opacity))
}

@media (min-width: 481px){
  .get-button-layout .get-button-container .input-container .input-label{
    margin-right:2rem;
    margin-bottom:0px
  }
}

@media (min-width: 481px){
  .get-button-layout .get-button-container .input-container .editable-input{
    width:66.666667%
  }
}

.get-button-layout .get-button-container .input-container .editable-input ::-webkit-input-placeholder{
  font-size:0.9rem
}

.get-button-layout .get-button-container .input-container .editable-input ::placeholder{
  font-size:0.9rem
}

.get-button-layout .get-button-container .input-container .editable-input .labeled-wrapper{
  min-height:2.5rem
}

.get-button-layout .get-button-container .input-container .editable-input .labeled-wrapper input{
  height:2.5rem
}

.get-button-layout .get-button-container .input-container .editable-input .label{
  --tw-text-opacity:1;
  color:rgba(41, 7, 89, var(--tw-text-opacity))
}

.get-button-layout .get-button-container .input-container .editable-input:not(:last-of-type){
  margin-bottom:1.5rem
}

.get-button-layout .get-button-container .radiogroup-whisper{
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  margin-left:auto;
  margin-right:auto;
  margin-top:2rem
}

.get-button-layout .get-button-container .mode-picker{
  padding-left:10px;
  border-color:rgba(0,0,0,0.23)
}

.get-button-layout .get-button-container .mode-picker .box-label{
  margin-top:auto;
  margin-bottom:auto;
  margin-right:0.75rem;
  font-size:0.875rem;
  line-height:1.25rem;
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content
}

.get-button-layout .get-button-container .mode-picker .rs-radio-checker label{
  padding-top:0.5rem;
  padding-bottom:0.5rem;
  --tw-text-opacity:0.7
}

.get-button-layout .get-button-container .mode-picker .rs-radio-checker label:hover{
  --tw-text-opacity:1;
  color:rgba(41, 7, 89, var(--tw-text-opacity))
}

.get-button-layout .get-button-container .mode-picker .rs-radio-checked label{
  --tw-text-opacity:1;
  color:rgba(41, 7, 89, var(--tw-text-opacity))
}

.get-button-layout .get-button-container .mode-picker .rs-radio-checked .rs-radio-checker{
  --tw-border-opacity:1;
  border-color:rgba(41, 7, 89, var(--tw-border-opacity));
  border-bottom-width:1.5px
}

.get-button-layout .get-button-container .buttons{
  margin-top:3rem;
  display:flex;
  width:100%;
  flex-direction:column;
  align-items:center
}

@media (min-width: 481px){
  .get-button-layout .get-button-container .buttons{
    flex-direction:row;
    justify-content:center
  }
}

.get-button-layout .get-button-container .buttons .rs-btn{
  width:10rem;
  font-weight:500;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

.get-button-layout .get-button-container .buttons .choose{
  margin-bottom:1rem;
  --tw-bg-opacity:1;
  background-color:rgba(41, 7, 89, var(--tw-bg-opacity));
  --tw-bg-opacity:0.7
}

.get-button-layout .get-button-container .buttons .choose:hover{
  --tw-bg-opacity:0.9
}

@media (min-width: 481px){
  .get-button-layout .get-button-container .buttons .choose{
    margin-right:2.5rem;
    margin-bottom:0px
  }
}

.get-button-layout .get-button-container .buttons .get-code{
  --tw-bg-opacity:1;
  background-color:rgba(0, 183, 196, var(--tw-bg-opacity));
  --tw-bg-opacity:0.7
}

.get-button-layout .get-button-container .buttons .get-code:hover{
  --tw-bg-opacity:1
}

.get-button-layout .get-button-container .code-container{
  position:relative;
  margin-top:2rem;
  animation:fadeIn 0.7s forwards
}

.get-button-layout .get-button-container .code-container pre{
  overflow-y:hidden;
  border-radius:0.5rem;
  padding-top:1rem !important;
  padding-bottom:1rem !important;
  background-color:#efecf4 !important
}

.get-button-layout .get-button-container .code-container .copy-icon{
  position:absolute;
  top:0.625rem;
  right:0.75rem
}

.get-button-layout .get-button-container .code-container .copy-icon .icon{
  width:1rem;
  cursor:pointer;
  --tw-text-opacity:1;
  color:rgba(41, 7, 89, var(--tw-text-opacity))
}

.get-button-layout .get-button-container .code-container .copy-icon .icon:hover{
  --tw-text-opacity:0.7
}

.get-button-layout .get-button-container .code-container .copy-icon .icon{
  transition-property:all;
  transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration:150ms
}

.get-button-layout .get-button-container .code-container .services-button{
  margin-top:1.5rem
}

.get-button-layout .get-button-container .code-container .services-button a:not(:last-of-type){
  margin-right:0.5rem
}

.copy-tooltip, .share-mode-tooltip{
  border-width:1px;
  --tw-border-opacity:1;
  border-color:rgba(41, 7, 89, var(--tw-border-opacity));
  --tw-text-opacity:1;
  color:rgba(41, 7, 89, var(--tw-text-opacity))
}

.copy-tooltip,.share-mode-tooltip{
  background-color:#f6f4f8
}

.copy-tooltip::after,.share-mode-tooltip::after{
  border-top-color:#290759 !important
}

.err-tooltip{
  border-width:1px;
  --tw-border-opacity:1;
  border-color:rgba(255, 59, 59, var(--tw-border-opacity));
  --tw-bg-opacity:1;
  background-color:rgba(255, 255, 255, var(--tw-bg-opacity));
  --tw-text-opacity:1;
  color:rgba(255, 59, 59, var(--tw-text-opacity))
}

.err-tooltip::after{
  border-top-color:#ff3b3b !important;
  border-bottom-color:#ff3b3b !important
}

.choose-services-modal .rs-modal-header{
  display:block;
  padding:0px;
  text-align:center;
  font-size:1.125rem;
  line-height:1.75rem;
  font-weight:500
}

.choose-services-modal .rs-modal-body{
  padding-bottom:1rem;
  overflow:visible !important
}

.choose-services-modal .rs-modal-body .services-list{
  overflow-y:auto
}

.choose-services-modal .rs-modal-body .services-list .service-radio .rs-radio-wrapper{
  top:auto
}

.choose-services-modal .rs-modal-body .services-list .service-radio .rs-radio-checker{
  padding-top:0px;
  padding-bottom:0px
}

.choose-services-modal .rs-modal-body .services-list .service-radio .rs-radio-inner::before{
  --tw-border-opacity:1;
  border-color:rgba(41, 7, 89, var(--tw-border-opacity));
  box-shadow:none
}

.choose-services-modal .rs-modal-body .services-list .service-radio.rs-radio-checked .rs-radio-inner::before{
  --tw-bg-opacity:1;
  background-color:rgba(41, 7, 89, var(--tw-bg-opacity))
}

.choose-services-modal .rs-modal-body .services-list .service-radio label{
  display:flex;
  align-items:center
}

.choose-services-modal .rs-modal-body .services-list .service-radio label .service-logo{
  border-radius:0.375rem;
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content
}

.choose-services-modal .rs-modal-body .services-list .service-radio label .service-logo img{
  height:1.5rem;
  width:1.5rem
}

@media (min-width: 481px){
  .choose-services-modal .rs-modal-body .services-list .service-radio label .service-logo img{
    height:2rem;
    width:2rem
  }
}

.choose-services-modal .rs-modal-body .services-list .service-radio label h2{
  margin-left:0.375rem;
  text-transform:capitalize;
  transition-property:background-color, border-color, color, fill, stroke;
  transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration:150ms
}

.choose-services-modal .rs-modal-footer{
  padding-top:1.5rem;
  text-align:center
}

.choose-services-modal .rs-modal-footer .rs-btn{
  margin-left:auto;
  margin-right:auto;
  width:10rem;
  --tw-bg-opacity:1;
  background-color:rgba(41, 7, 89, var(--tw-bg-opacity));
  --tw-bg-opacity:0.6
}

.choose-services-modal .rs-modal-footer .rs-btn:hover{
  --tw-bg-opacity:0.8
}

.choose-services-modal .rs-modal-footer .rs-btn{
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

